import React, {Component} from 'react';
import BoxList from '../Components/BoxList';
import {Ghosts} from '../Ghosts';
import Searchbar from '../Components/Searchbar';
import Scroll from '../Components/Scroll';

class App extends Component { constructor (){
    super()
    this.state={ Ghosts:Ghosts, searchview: ''

    }

    

}

searchChange = (event) =>{
    this.setState({searchview:event.target.value})}



render() {

    const filteredGhosts= this.state.Ghosts.filter(Ghosts =>{return Ghosts.name.toLowerCase().includes(this.state.searchview.toLowerCase())})

return(
<div className = 'tc'>
    <h1 className ='tc'>Delve into the various mysteries</h1>
    <Searchbar searchChanger={this.searchChange}/>
   <Scroll> <BoxList Ghosts = {filteredGhosts}/></Scroll>
    </div>
);}}

export default App;


