import React from 'react';
import Box from './Box';

const BoxList = ({Ghosts}) => {  return( <div>{

Ghosts.map((user,i) => {
    return(
        <Box 
        key= {i}
        id= {Ghosts[i].id}
        name = {Ghosts[i].name}
        image= {Ghosts[i].image}
        event= {Ghosts[i].event}
        
        />

    );
})
    
    
    
}

</div>

);}

export default BoxList;
